import React, { useState, useEffect, Fragment } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  increment,
  fetchAllProductsAsync,
  selectAllProducts,
  fetchProductsByFiltersAsync,
  selectTotalItems,
  selectCategories,
  selectBrands,
  fetchBrandsAsync,
  fetchCategoriesAsync,
  selectProductListStatus
} from '../productSlice';
import { Link } from "react-router-dom";
import { Dialog, Disclosure, Menu, Transition } from '@headlessui/react'
import { XMarkIcon } from '@heroicons/react/24/outline'
import { ChevronDownIcon, FunnelIcon, MinusIcon, PlusIcon, Squares2X2Icon, StarIcon } from '@heroicons/react/20/solid'
import { ITEMS_PER_PAGE, discountedPrice } from '../../../app/constants';
import Pagination from '../../common/Pagination';
import { fetchBrands } from '../productAPI';
// import { Grid } from 'react-loader-spinner';
import ProductGrid from "./ProductGrid"
import PriceRangeFilter from './PriceRangeFilter';
function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}
export default function ProductList() {
  const products = useSelector(selectAllProducts);
  console.log("products : ",products)
  const totalItems = useSelector(selectTotalItems);
  const categories = useSelector(selectCategories);
  const brands = useSelector(selectBrands);
  const sizes =  [
      { value: 's', label: 'S', checked: false },
      { value: 'm', label: 'M', checked: false },
      { value: 'l', label: 'L', checked: false },
      { value: 'xl', label: 'XL', checked: false },
      { value: 'xxl', label: 'XXL', checked: false },
    ];
  const colors = [
    
      { value: 'white', label: 'White', checked: false },
      { value: 'beige', label: 'Beige', checked: false },
      { value: 'blue', label: 'Blue', checked: true },
      { value: 'brown', label: 'Brown', checked: false },
      { value: 'green', label: 'Green', checked: false },
      { value: 'purple', label: 'Purple', checked: false },
    
  ] ; 
  const dispatch = useDispatch();

  const filters = [
    {
      id: 'category',
      name: 'Category',
      options: categories,
    },
    {
      id: 'brand',
      name: 'Brands',
      options: brands,
    },
    {
      id:'size',
      name:'sizes',
      options:sizes
    },
    {id:'color',
    name:'colors',
    options:colors
    },

     
  ]
  const [filter, setFilter] = useState({});
  const [sort, setSort] = useState({});
  const [mobileFiltersOpen, setMobileFiltersOpen] = useState(false)
  const [page, setPage] = useState(1);
  const status = useSelector(selectProductListStatus);

  const handleFilter = (e, section, option) => {
  
    // e.target.checked
    const newFilter = { ...filter };
    // console.log("kk",newFilter);
    if (e.target.checked) {
      if (newFilter[section.id]) {
        newFilter[section.id].push(option.value);

      } else {
        newFilter[section.id] = [option.value];
      }
    } else {
      //unchecked
      const index = newFilter[section.id].findIndex(el => el === option.value);
      newFilter[section.id].splice(index, 1);
    }

    // console.log({newFilter})
    setFilter(newFilter);
    //
  }

  const handleSort = (e, option) => {
    const sort = { _sort: option.sort, _order: option.order };

    // console.log({sort})
    setSort(sort);
    //
  }
  const handlePage = (page) => {
    // console.log(page)
    setPage(page);
  }

  useEffect(() => {
    dispatch(fetchBrandsAsync())
    dispatch(fetchCategoriesAsync())

  }, [])

  // useEffect(() => {
  //   setPage(1)
  // }, [totalItems, sort])
  useEffect(() => {
    setPage(1)
  }, [totalItems, sort])

  useEffect(() => {
    const pagination = {
      _page: page, _limit: ITEMS_PER_PAGE
    }
    
    dispatch(fetchProductsByFiltersAsync({ filter, sort,pagination }))
    console.log("filter, sort, pagination", {filter, sort, pagination })
  }, [dispatch, filter,sort, page]);

  const sortOptions = [
    { name: 'Best Rating', sort: 'rating', order: 'desc', current: false },
    { name: 'Price: Low to High', sort: 'price', order: 'asc', current: false },
    { name: 'Price: High to Low', sort: 'price', order: 'desc', current: false },
  ];

  return (
   
        <div className="bg-white">
        
          <div>
            {/* Mobile filter dialog */}
            <MobileFilter filters={filters} handleFilter={handleFilter} mobileFiltersOpen={mobileFiltersOpen} setMobileFiltersOpen={setMobileFiltersOpen}></MobileFilter>

            <main className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
              <div className="flex items-baseline justify-between border-b border-gray-200 pb-6 pt-5">
                <h2 className="text-2xl font-bold tracking-tight sm:text-3xl  textplayfair">Product Filters</h2>

                <div className="flex items-center">
                  <Menu as="div" className="relative inline-block text-left">
                    <div>
                      <Menu.Button className="group inline-flex justify-center text-sm font-medium text-gray-700 hover:text-gray-900">
                        Sort
                        <ChevronDownIcon
                          className="-mr-1 ml-1 h-5 w-5 flex-shrink-0 text-gray-400 group-hover:text-gray-500"
                          aria-hidden="true"
                        />
                      </Menu.Button>
                    </div>

                    <Transition
                      as={Fragment}
                      enter="transition ease-out duration-100"
                      enterFrom="transform opacity-0 scale-95"
                      enterTo="transform opacity-100 scale-100"
                      leave="transition ease-in duration-75"
                      leaveFrom="transform opacity-100 scale-100"
                      leaveTo="transform opacity-0 scale-95"
                    >
                      <Menu.Items className="absolute right-0 z-10 mt-2 w-40 origin-top-right rounded-md bg-white shadow-2xl ring-1 ring-black ring-opacity-5 focus:outline-none">
                        <div className="py-1">
                          {sortOptions.map((option) => (
                            <Menu.Item key={option.name}>
                              {({ active }) => (
                                <p
                                  onClick={e => handleSort(e, option)}
                                  className={classNames(
                                    option.current ? 'font-medium text-gray-900' : 'text-gray-500',
                                    active ? 'bg-gray-100' : '',
                                    'block px-4 py-2 text-sm'
                                  )}


                                >

                                  {option.name}
                                </p>
                              )}
                            </Menu.Item>
                          ))}
                        </div>
                      </Menu.Items>
                    </Transition>
                  </Menu>

                  <button type="button" className="-m-2 ml-5 p-2 text-gray-400 hover:text-gray-500 sm:ml-7">
                    <span className="sr-only">View grid</span>
                    <Squares2X2Icon className="h-5 w-5" aria-hidden="true" />
                  </button>
                  <button
                    type="button"
                    className="-m-2 ml-4 p-2 text-gray-400 hover:text-gray-500 sm:ml-6 lg:hidden"
                    onClick={() => setMobileFiltersOpen(true)}
                  >
                    <span className="sr-only">Filters</span>
                    <FunnelIcon className="h-5 w-5" aria-hidden="true" />
                  </button>
                </div>
              </div>

              <section aria-labelledby="products-heading" className="pb-24 pt-6">
                <h2 id="products-heading" className="sr-only">
                  Products
                </h2>

                <div className="grid grid-cols-1 gap-x-0 gap-y-10 lg:grid-cols-4">
                  {/* Filters */}
                  <DesktopFilter filters={filters} handleFilter={handleFilter}></DesktopFilter>

                  {/* Product grid */}
                  <div className="lg:col-span-3">
                    {/*Product list page starts*/}
                    <ProductGrid products={products} status={status} totalItems={totalItems} ></ProductGrid>
                    {/*Product list page ends*/}

                  </div>
                </div>
                {/*Pagination starts*/}
                <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
                  {/* {console.log("inner ",page)} */}
                  <Pagination totalItems={totalItems} page={page} setPage={setPage} handlePage={handlePage} />
                </div>
                {/* Pagination Ends here */}
              </section>
            </main>
          </div>
        </div>


    
  );
}

function DesktopFilter({ filters, handleFilter }) {
  return <>
    <form className="hidden lg:block">
      <h3 className="sr-only">Categories</h3>



      {filters.map((section) => (
        <Disclosure as="div" key={section.id} className="border-b border-gray-200 py-6">
          {({ open }) => (
            <>
              <h3 className="-my-3 flow-root">
                <Disclosure.Button className="flex w-full items-center justify-between bg-white py-3 text-sm text-gray-400 hover:text-gray-500">
                  <span className="font-medium text-gray-900">{section.name}</span>
                  <span className="ml-6 flex items-center">
                    {open ? (
                      <MinusIcon className="h-5 w-5" aria-hidden="true" />
                    ) : (
                      <PlusIcon className="h-5 w-5" aria-hidden="true" />
                    )}
                  </span>
                </Disclosure.Button>
              </h3>
              <Disclosure.Panel className="pt-6">
                <div className="space-y-4">
                  {section.options.map((option, optionIdx) => (
                    <div key={option.value} className="flex items-center">
                      <input
                        id={`filter-${section.id}-${optionIdx}`}
                        name={`${section.id}[]`}
                        defaultValue={option.value}
                        type="checkbox"
                        defaultChecked={option.checked}
                        onChange={e => handleFilter(e, section, option)} className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                      />
                      <label
                        htmlFor={`filter-${section.id}-${optionIdx}`}
                        className="ml-3 text-sm text-gray-600"
                      >
                        {option.label}
                      </label>
                    </div>
                  ))}
                </div>
              </Disclosure.Panel>
            </>
          )}
        </Disclosure>
      ))}

      {/*price range starts*/}
      <p className="mt-8 text-sm">Filter by Price</p>
       <PriceRangeFilter></PriceRangeFilter>
      {/*price range ends */}
    </form>
  </>
}
function MobileFilter({ mobileFiltersOpen, setMobileFiltersOpen, handleFilter, filters }) {

  return <>
    <Transition.Root show={mobileFiltersOpen} as={Fragment}>
      <Dialog as="div" className="relative z-40 lg:hidden" onClose={setMobileFiltersOpen}>
        <Transition.Child
          as={Fragment}
          enter="transition-opacity ease-linear duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="transition-opacity ease-linear duration-300"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-black bg-opacity-25" />
        </Transition.Child>

        <div className="fixed inset-0 z-40 flex">
          <Transition.Child
            as={Fragment}
            enter="transition ease-in-out duration-300 transform"
            enterFrom="translate-x-full"
            enterTo="translate-x-0"
            leave="transition ease-in-out duration-300 transform"
            leaveFrom="translate-x-0"
            leaveTo="translate-x-full"
          >
            <Dialog.Panel className="relative ml-auto flex h-full w-full max-w-xs flex-col overflow-y-auto bg-white py-4 pb-12 shadow-xl mobilefilter" >
              <div className="flex items-center justify-between px-4">
                <h2 className="text-lg font-medium text-gray-900">Filters</h2>
                <button
                  type="button"
                  className="-mr-2 flex h-10 w-10 items-center justify-center rounded-md bg-white p-2 text-gray-400"
                  onClick={() => setMobileFiltersOpen(false)}
                >
                  <span className="sr-only">Close menu</span>
                  <XMarkIcon className="h-6 w-6" aria-hidden="true" />
                </button>
              </div>

              {/* Filters */}
              <form className="mt-4 border-t border-gray-200">
                <h3 className="sr-only">Categories</h3>


                {filters.map((section) => (
                  <Disclosure as="div" key={section.id} className="border-t border-gray-200 px-4 py-6">
                    {({ open }) => (
                      <>
                        <h3 className="-mx-2 -my-3 flow-root">
                          <Disclosure.Button className="flex w-full items-center justify-between bg-white px-2 py-3 text-gray-400 hover:text-gray-500">
                            <span className="font-medium text-gray-900">{section.name}</span>
                            <span className="ml-6 flex items-center">
                              {open ? (
                                <MinusIcon className="h-5 w-5" aria-hidden="true" />
                              ) : (
                                <PlusIcon className="h-5 w-5" aria-hidden="true" />
                              )}
                            </span>
                          </Disclosure.Button>
                        </h3>
                        <Disclosure.Panel className="pt-6">
                          <div className="space-y-6">
                            {section.options.map((option, optionIdx) => (
                              <div key={option.value} className="flex items-center">
                                <input
                                  id={`filter-mobile-${section.id}-${optionIdx}`}
                                  name={`${section.id}[]`}
                                  defaultValue={option.value}
                                  type="checkbox"
                                  defaultChecked={option.checked}
                                  onChange={e => handleFilter(e, section, option)}
                                  className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                                />
                                <label
                                  htmlFor={`filter-mobile-${section.id}-${optionIdx}`}
                                  className="ml-3 min-w-0 flex-1 text-gray-500"
                                >
                                  {option.label}
                                </label>
                              </div>
                            ))}
                          </div>
                        </Disclosure.Panel>
                      </>
                    )}
                  </Disclosure>
                ))}

                 {/*price range starts*/}
      <p className="mt-8 text-sm">Filter by Price</p>
       <PriceRangeFilter></PriceRangeFilter>
      {/*price range ends */}
              </form>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition.Root>
  </>
}

